export default {
  data() {
    return {
		loading: false,
	  title: "健康南县",
	  loginname:"",
	  password:"",
	  res:""
    };
  },
  onLoad() {},
  methods: {
    go_resetpasswd() {
      uni.navigateTo({
        url: "./resetpasswd"
      });
    },
    go_register() {
      uni.navigateTo({
        url: "./register"
      });
	},
	async go_login(){
		 
				this.loading = true
               let res = await this. $insunapi.userlogin({loginname: this.loginname,password:this.password});
						
                    this.loading = false;
                    console.log('request success', res)
                   

	},
	loginnameInput(e) {
				this.loginname = e.detail.value
			},
passwordInput(e) {
				this.password = e.detail.value
			},
			
  }
};
